.services-section {
  padding: 3rem 0;
  background-color: #f8f8f8;
}
section {
  min-height: 100vh; /* Ensures sections are tall enough */
}

.services-section h2 {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 2rem;
}

.services-inner {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

.services-content {
  text-align: center;
}

.collection-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  justify-items: center;
}

/* Common styles for the flip card */
.grid-item {
  width: 100%;
  max-width: 300px;
  perspective: 1000px;
  cursor: pointer;
}

/* Default card flip settings */
.flip-card {
  width: 100%;
  height: 350px;
  position: relative;
  transform-style: preserve-3d;
  transition: transform 0.6s;
}

.flip-card-front,
.flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.flip-card-front {
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
}

.flip-card-back {
  background-color: #333;
  color: #fff;
  transform: rotateY(180deg); /* Back is rotated by default */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 1rem;
  z-index: 1;
}

/* Image style */
.picture-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.grid-item:hover .picture-image {
  transform: scale(1.05); /* Optional hover effect */
}

.service-title {
  font-family: "Montserrat", sans-serif;
  font-size: 1.75rem;
  font-weight: 700;
  color: #d4a5a5;
  margin-bottom: 0.5rem;
}

.service-description {
  font-family: "Roboto", sans-serif;
  font-size: 1.1rem;
  text-align: center;
  color: #e0e0e0;
  line-height: 1.5;
}

/* Media query for mobile view */
@media (max-width: 768px) {
  .flip-card {
    transform-style: preserve-3d;
  }

  .grid-item {
    /* Ensure full width for mobile view */
    width: 100%;
  }
  .grid-item.flipped .flip-card {
    transform: rotateY(180deg); /* Flip on click for mobile */
  }

  /* Ensure box-shadow is applied correctly */
  .grid-item:hover .flip-card-front,
  .grid-item:hover .flip-card-back {
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
  }
}

/* Media query for mobile view */
@media (min-width: 768px) {
  .grid-item:hover .flip-card {
    transform: rotateY(180deg);
  }
}
