.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 50px;
  background-color: rgba(0, 0, 0, 0.7);
  padding: 1rem 2rem;
  z-index: 50;
}

.logo img {
  height: auto;
  width: 100px;
}

.menuButton {
  display: flex;
  flex-direction: column;
  gap: 4px;
  cursor: pointer;
}

.menuButton .bar {
  width: 25px;
  height: 3px;
  background-color: white;
  transition: all 0.3s ease;
}

.menuButton.open .bar:nth-child(1) {
  transform: rotate(-45deg) translate(-5px, 6px);
}

.menuButton.open .bar:nth-child(2) {
  opacity: 0;
}

.menuButton.open .bar:nth-child(3) {
  transform: rotate(45deg) translate(-5px, -6px);
}

.menu {
  display: none;
  position: absolute;
  top: 50px;
  right: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  /* border-radius: 0 0 0 10px; */
  z-index: 100;
}

.menu.open {
  display: block;
}

.menu ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.menu ul li {
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.menu ul li:last-child {
  border-bottom: none;
}

.menu ul li a {
  display: block;
  padding: 15px;
  color: white;
  text-decoration: none;
  font-size: 1rem;
  margin: 0.5rem;
  text-align: center;
}

.menu ul li a:hover,
.menu ul li a.active {
  background-color: rgba(255, 255, 255, 0.2);
  transition: background-color 0.3s ease;
}
@media (min-width: 769px) {
  .menuButton {
    display: none;
  }

  .menu {
    display: flex;
    position: static;
    background: transparent;
    border-radius: none;
    width: auto;
  }

  .menu ul {
    display: flex;
    flex-direction: row;
    padding: 0;
    margin: 0;
  }

  .menu ul li {
    border: none;
  }

  .menu ul li a {
    padding: 10px;
  }
}
